@import url('../components/personal-plan.css');
@import url('../components/cart.css');
@import url('../components/seo-custom.css');


.header{
  background: #0B0B0B;
}

.title-main{
  margin-bottom: 30px;
  margin-top: 70px;
  @media (max-width: 767.98px) {
    margin-top: 35px;
  }
  .subtitle {
    p {
      margin-bottom: 3px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
    }
  }
  h1{
    color: #0B0B0B;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    @media (max-width: 767.98px) {
      font-size: 30px;
      font-weight: 600;
      line-height: normal;
    }
  }
}

.cases {
  background: #F6F6F8;
  padding-top: 110px;
  @media (max-width: 767.98px) {
    padding-top: 74px;
  }
  &__category {
    display: flex;
    flex-wrap: wrap;
  }
  &__category-name{
    padding: 8px 16px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
    border-radius: 4px;
    margin-bottom: 16px;
    transition: 0.5s;
    a{
      color: #0B0B0B;
      text-align: center;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
    &:hover{
      background: #7339F5;
      color: #fff;
      cursor: pointer;
      a{
        color: #fff;
      }
    }

  }
  &__category-name.active{
    background: #7339F5;
    a{
      color: #fff;
    }

  }
}

.cases__list {
  margin: 2px 0px 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  @media (max-width: 1023.98px) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media (max-width: 767.98px) {
    grid-template-columns: repeat(1, 1fr);
  }
}
